@import url(https://starlingui.netlify.app/sui.css);
html[sui-mode="dark"] {
  --PRIMARY: var(--PRIMARY-LIGHT2);
}
body {
  background: var(--BG);
  color: var(--TEXT);
}

body {
  display: grid;
  font-size: 1.6rem;
  grid-template-areas:
    "header"
    "main"
    "footer";
  grid-template-rows: auto 1fr auto;
  position: relative;
  height: 100vh;
  overflow-x: hidden;
}
main {
  grid-area: main;
  display: grid;
  grid-template-areas:
    "carousel"
    "hcontent";
  grid-template-rows: 50rem 1fr;
}
.carousel {
  grid-area: carousel;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  overflow: hidden;
  background: var(--SHADE);
  position: relative;
}
@media only screen and (max-width: 768px) {
  .carousel {
    grid-template-columns: 0 2fr 0;
  }
}
.parallax_slider {
  transition: opacity 0.4s ease-in;
  -webkit-transition: opacity 0.4s ease-in;
  -moz-transition: opacity 0.4s ease-in;
  -ms-transition: opacity 0.4s ease-in;
  -o-transition: opacity 0.4s ease-in;
  opacity: 0;
}
.transitioning_src {
  transition: opacity 0.4s ease-out;
  -webkit-transition: opacity 0.4s ease-out;
  -moz-transition: opacity 0.4s ease-out;
  -ms-transition: opacity 0.4s ease-out;
  -o-transition: opacity 0.4s ease-out;
  opacity: 1;
}
.home_content {
  grid-area: hcontent;
  width: 100%;
  align-items: center;
}
.carousel img {
  width: 100%;
  height: 100%;
}
.carousel_btn {
  position: absolute;
  bottom: 2rem;
  left: calc(50% - 13rem);
  font-size: 3rem;
  width: 26rem;
}
.stc_feat {
  width: 100%;
  margin-top: 6rem;
  font-size: 2.5rem;
  font-weight: 500;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
}
.feat_wrapper {
  margin-top: 2rem;
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
}
.pop_wrapper {
  max-width: var(--MAX-WIDTH);
  margin: 2rem 2rem 0;
  grid-gap: 2rem;
  gap: 2rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
.pop_wrapper img {
  width: 100%;
}
.pop_wrapper img:hover {
  cursor: pointer;
  box-shadow: 0 0 4px 0 var(--TEXT);
}
.pop_cat {
  position: relative;
}
.pop_cat span {
  position: absolute;
  bottom: 0;
  width: 100%;
  text-align: center;
  background: var(--SHADE);
  opacity: 0.7;
}

header.sui_nav_mobile {
  grid-area: header;
  box-shadow: 0 0 1px 1px var(--SHADE);
  z-index: 1;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}
.sui_logo_wrapper {
  cursor: pointer;
}
header,
header input,
header .sui_input_lbl,
header .sui_input_info {
  background: var(--BG);
}
.sui_nav_mobile .sui_cta svg {
  width: 2.5rem;
  height: 2.5rem;
}
header .sui_btn {
  font-size: 1.2rem;
}
.sui_input input {
  padding: 6px 15px 4px 15px;
  border: 1px solid var(--TEXT);
}
.input_icon input {
  padding-left: 3.6rem;
}
.sui_cta {
  grid-gap: 2rem;
  gap: 2rem;
}
.bd_num {
  color: var(--WHITE);
}
.btn_icon_fa {
  display: flex;
  align-items: center;
}
.btn_icon_fa:hover {
  cursor: pointer;
}
#stc-profile {
  font-size: 2.2rem;
  position: relative;
}
.user_profile_dropdown_menu {
  display: none;
  position: absolute;
  bottom: -6rem;
  right: -2rem;
  background: var(--BG);
  padding: 1rem;
  border-radius: 0 0 8px 8px;
  z-index: 2;
}
.show_dropdown {
  display: revert;
}
.sui_nav_mobile .fa-bars {
  display: none;
}
@media only screen and (max-width: 768px) {
  :root {
    font-size: 9px;
  }
  body {
    grid-template-rows: 12rem 1fr auto;
  }
  header.sui_nav_mobile {
    padding: 1rem 2rem;
  }
}
@media only screen and (max-width: 600px) {
  :root {
    font-size: 8px;
  }
  main .filter_area {
    margin-left: 0;
  }
  .sui_nav_mobile .sui_cta {
    display: flex;
    flex-direction: row;
    position: relative;
    background: var(--BG);
    top: 0;
    right: 0;
    z-index: 1;
    padding: 2rem;
    border-radius: 8px;
  }
}
@media only screen and (max-width: 400px) {
  .sui_nav_mobile .fa-bars {
    display: revert;
  }
  .sui_nav_mobile .sui_cta {
    box-shadow: 0 0 1px 1px var(--SHADE);
    flex-direction: row;
    position: absolute;
    background: var(--BG);
    top: 5rem;
    right: -1rem;
    z-index: 9;
    padding: 2rem;
    border-radius: 8px;
  }
  .sui_nav_mobile .hide_cta {
    display: none;
  }
}

footer {
  grid-area: footer;
  position: relative;
  width: 100vw;
  background: var(--SHADE);
  padding: 2rem 0;
  margin-top: 6rem;
  grid-gap: 2rem;
  gap: 2rem;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: center;
}
footer .sui_logo {
  width: 4rem;
}
footer .sui_logo_text,
footer .sui_btn {
  color: var(--TEXT);
  line-height: 1;
  text-decoration: none;
}
footer .stc_logo_caption {
  color: var(--PRIMARY);
}
footer .sui_btn {
  font-size: 1.4rem;
  line-height: 1.5;
  padding: 0;
}
.ftr_title {
  font-size: 1.6rem;
  padding-bottom: 1rem;
  color: var(--GRAY);
}

